.benefits {
    padding-top: 80px;
    @include xs {
        padding-top: 20px; }
    &__title {}
    &__subtitle {
        max-width: 520px;
        margin: 24px auto 0;
        text-align: center;
        @include xs {
            margin-top: 10px; } }
    &__content {
        margin-top: 119px;
        display: flex;
        flex-wrap: wrap;
        @include sm {
            margin-top: 30px; } }
    &__item {
        width: 50%;
        @include sm {
            width: 100%; } }

    &-item {
        padding: 40px 30px 99px;
        text-align: center;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        transition: box-shadow .3s;
        @include sm {
            padding: 20px; }
        &:hover {
            box-shadow: 0 4px 15px 0 rgba(7, 42, 68, 0.1); }
        &__icon {
            flex: 1 0 auto;
            display: flex;
            align-items: center;
            justify-content: center;
            img {
                display: block;
                max-width: 100%; } }
        &__title {
            flex: 0 0 auto;
            margin-top: 24px;
            font-size: 24px;
            color: $dark;
            @include sm {
                margin-top: 10px; } }
        &__text {
            flex: 0 0 auto;
            margin-top: 40px;
            color: $darkGray;
            @include sm {
                margin-top: 10px; } } } }
