}

*[data-anim-direction^='ltr'] {
  transform: translateY(0) translateX(-75px);
}

*[data-anim-direction^='rtl'] {
  transform: translateY(0) translateX(75px);
}

*[data-anim-direction^='top'] {
  transform: translateY(-75px) translateX(0);
}

