/*Sandwich*/
.toggle-menu {
  position: relative;
  float: right;
  width: 32px;
  height: 32px;
  display: none;
  z-index: 100;
  border: none;
  background-color: transparent;
}

@media only screen and (max-width: 767px) {
  .toggle-menu {
    display: block;
  }
}

.toggle-menu:before {
  content: "";
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}

.toggle-menu:focus {
  outline: none;
}

.sandwich {
  width: 32px;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 200;
  top: 50%;
  transform: translateY(-50%);
}

.sw-topper {
  display: block;
  position: relative;
  transform-origin: left top;
  width: 32px;
  height: 3px;
  background: #fff;
  border: none;
  transition: transform 0.5s, background-color .5s;
  margin-bottom: 5px;
}

@media only screen and (max-width: 599px) {
  .sw-topper {
    margin-bottom: 4px;
  }
}

.header.is-fixed .sw-topper,
.header.is-open-menu .sw-topper {
  background-color: #363636;
}

.sw-bottom {
  overflow: hidden;
  display: block;
  position: relative;
}

.sw-bottom__inner {
  display: block;
  position: relative;
  width: 32px;
  height: 3px;
  transform-origin: center center;
  background: #fff;
  border: none;
  transition: transform 0.3s ease-in-out, opacity .5s ease-in-out, background-color .5s;
  margin-bottom: 5px;
}

.header.is-fixed .sw-bottom__inner,
.header.is-open-menu .sw-bottom__inner {
  background-color: #363636;
}

@media only screen and (max-width: 599px) {
  .sw-bottom__inner {
    margin-bottom: 4px;
  }
}

.sw-footer {
  display: block;
  position: relative;
  width: 32px;
  height: 3px;
  transform-origin: left;
  background: #fff;
  border: none;
  transition: transform 0.5s, background-color .5s;
}

.header.is-fixed .sw-footer,
.header.is-open-menu .sw-footer {
  background-color: #363636;
}

.sandwich.is-active .sw-topper {
  transform: translate(0, -3px) rotate(45deg);
}

.sandwich.is-active .sw-bottom__inner {
  opacity: 0;
  transform: translateX(-105%);
}

.sandwich.is-active .sw-footer {
  transform: translate(0, 3px) rotate(-45deg);
}

@media only screen and (max-width: 599px) {
  .sandwich.is-active .sw-footer {
    transform: translate(0, 5px) rotate(-45deg);
  }
}

