.portfolio__control {
  position: relative;
  cursor: pointer;
  user-select: none;
}

.portfolio__control:not(:first-child) {
  margin-left: 30px;
}

.portfolio__control:before {
  content: "";
  position: absolute;
  left: 50%;
  width: 115%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 1px;
  opacity: 0;
  visibility: hidden;
  background-color: #0074e4;
  transition: opacity .3s, visibility .3s;
}

.portfolio__control.is-active:before {
  opacity: 1;
  visibility: visible;
}

.portfolio__list-wrap {
  margin: 0 -12px;
}

.portfolio-item {
  width: 50%;
  padding: 12px;
}

@media only screen and (max-width: 767px) {
  .portfolio-item {
    width: 100%;
  }
}

.portfolio-item__wrap {
  position: relative;
  transition: box-shadow .3s;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.15);
}

.portfolio-item__wrap:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(to left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.86) 73%, #ffffff);
}

.no-touch .portfolio-item__wrap:hover {
  box-shadow: 0 11px 33px 0 rgba(7, 42, 68, 0.3);
}

.portfolio-item__title, .portfolio-item__link {
  position: absolute;
  z-index: 1;
}

.portfolio-item__img {
  padding: 10px;
}

.portfolio-item__img img {
  display: block;
  max-width: 100%;
}

@media only screen and (max-width: 767px) {
  .portfolio-item__img img {
    width: 100%;
  }
}

.portfolio-item__title {
  font-size: 24px;
  color: #0074e4;
  font-weight: 400;
  left: 32px;
  top: 35px;
  max-width: 250px;
}

.portfolio-item__link {
  left: 32px;
  bottom: 0;
}

.contacts {
  padding: 108px 0 145px;
}

@media only screen and (max-width: 767px) {
  .contacts {
    padding: 30px 0;
  }
}

.contacts__wrap {
  position: relative;
  padding: 99px 0 111px;
  background-image: linear-gradient(to bottom, #20232d, #151720);
}

@media only screen and (max-width: 767px) {
  .contacts__wrap {
    padding: 30px 0;
  }
}

.contacts__content {
  position: relative;
  z-index: 5;
}

.contacts__text {
  max-width: 490px;
  color: #fff;
  width: 48%;
  padding-right: 11px;
}

@media only screen and (max-width: 1023px) {
  .contacts__text {
    max-width: none;
    width: 100%;
    padding: 0;
  }
}

.contacts__title {
  text-align: left;
  line-height: 1.35;
}

@media only screen and (max-width: 1023px) {
  .contacts__title {
    text-align: center;
  }
}

.contacts__list {
  margin-top: 56px;
}

@media only screen and (max-width: 1023px) {
  .contacts__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media only screen and (max-width: 767px) {
  .contacts__list {
    margin-top: 20px;
  }
}

.contacts__item:not(:first-child) {
  margin-top: 37px;
}

@media only screen and (max-width: 767px) {
  .contacts__item:not(:first-child) {
    margin-top: 10px;
  }
}

.contacts__feedback {
  max-width: 546px;
  width: 52%;
  position: absolute;
  top: -22px;
  right: 0;
  z-index: 5;
}

@media only screen and (max-width: 1023px) {
  .contacts__feedback {
    position: initial;
    max-width: none;
    width: 100%;
    margin-top: 40px;
  }
}

.contacts-item {
  position: relative;
  padding-left: 46px;
}

.contacts-item__icon {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.contacts-item__icon_phone {
  font-size: 20px;
}

.contacts-item__icon_email {
  font-size: 15px;
}

.contacts-item__icon_telegram {
  font-size: 22px;
}

.contacts-item__icon .icon {
  display: block;
}

.contacts-icons {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.contacts-icons__inner {
  position: relative;
  height: 100%;
}

.contacts-icons__svg {
  left: 205px;
  bottom: 25px;
  z-index: 2;
}

.contacts-icons__svg_blurred {
  filter: blur(10.2px);
  left: 115px;
  bottom: -60px;
  font-size: 200px;
  z-index: 1;
}

.contacts-icons__svg_small {
  filter: blur(10.2px);
  font-size: 165px;
  right: 99px;
  bottom: auto;
  top: -35px;
  left: auto;
}

.feedback {
  padding: 51px 73px 55px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 20px 0 rgba(29, 17, 86, 0.12);
}

@media only screen and (max-width: 599px) {
  .feedback {
    padding: 20px;
  }
}

.feedback__title {
  text-align: center;
  line-height: 1.35;
}

.feedback__form {
  margin-top: 40px;
}

@media only screen and (max-width: 599px) {
  .feedback__form {
    margin-top: 20px;
  }
}

.feedback-form__item:not(:first-child) {
  margin-top: 15px;
}

.feedback-form__submit {
  min-height: 50px;
  max-width: 300px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px auto 0;
}

@media only screen and (max-width: 767px) {
  .feedback-form__submit {
    min-height: 80px;
  }
}

.feedback-form__progress-bar {
  pointer-events: none;
  position: absolute;
  height: 10px;
  width: 0;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 200px;
  background: #0074e4;
}

.feedback-form__checkmark {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
  left: 50%;
  right: 0;
}

.feedback-form__checkmark .icon {
  font-size: 40px;
  fill: transparent;
  stroke: #fff;
  stroke-width: 2px;
  stroke-dasharray: 42px;
  stroke-dashoffset: 42px;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.form-input {
  width: 100%;
}

.input {
  display: block;
  width: 100%;
  height: 50px;
  padding: 15px 20px;
  border-radius: 4px;
  border: 1px solid rgba(13, 50, 93, 0.2);
  color: #89969f;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 14px;
}

.input::placeholder {
  color: #89969f;
  font-size: 14px;
}

textarea.input {
  height: 157px;
  resize: none;
}

.footer {
  padding: 80px 0 32px;
}

@media only screen and (max-width: 767px) {
  .footer {
    padding: 20px 0;
  }
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 599px) {
  .footer__inner {
    flex-direction: column-reverse;
  }
}

@media only screen and (max-width: 599px) {
  .footer__copyright {
    margin-top: 20px;
  }
}

.social {
  display: flex;
}

.social__item:not(:first-child) {
  margin-left: 30px;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #0074e4;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 1px solid rgba(0, 116, 228, 0.2);
}

.social__link_facebook {
  font-size: 20px;
}

.social__link_twitter {
  font-size: 16px;
}

.social__link_telegram {
  font-size: 17px;
}
