body {
	position: relative;
	font-family: $roboto;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.56;
	min-width: 320px;
	color: $black;
	background-color: $white; }

.title {
	font-size: 38px;
	color: $blue;
	text-align: center;
	font-weight: 400;
	@include sm {
		font-size: 24px; }
	&_lg {
		font-size: 45px;
		line-height: 1.11;
		@include sm {
			font-size: 32px; } }
	&_sm {
		font-size: 32px; }
	&_xs {
		font-size: 20px;
		line-height: 2.5;
		letter-spacing: -0.8px; }
	&_white {
		color: $white; }
	&_dark {
		color: $dark; } }

.view-button {
	display: flex;
	align-items: center;
	height: 50px;
	font-size: 14px;
	text-transform: uppercase;
	@include sm {
		height: 30px; }
	.icon {
		margin-left: 15px;
		font-size: 9px; }
	&_blue {
		color: $blue; } }

.submit-button {
	border-radius: 4px;
	font-size: 14px;
	height: 50px;
	color: $white;
	padding: 15px 46px;
	text-transform: uppercase;
	background-image: linear-gradient(258deg, #18baeb, #0074e4); }
